import React, { useReducer, useRef } from 'react';

import { useFetch, useInfiniteScroll, useLazyLoading } from './hooks'
import { Link } from 'react-router-dom';

const Products = (props) => {

    const imgReducer = (state, action) => {
        switch (action.type) {
            case 'STACK_IMAGES':
                return { ...state, images: state.images.concat(action.images) }
            case 'FETCHING_IMAGES':
                return { ...state, fetching: action.fetching }
            default:
                return state;
        }
    }

    const pageReducer = (state, action) => {
        switch (action.type) {
            case 'ADVANCE_PAGE':
                return { ...state, page: state.page + 1 }
            case 'STOP_FETCHING':
                return { ...state, search: 1 }
            default:
                return state;
        }
    }

    const [pager, pagerDispatch] = useReducer(pageReducer, { page: 0, search: 0, productListUrl: props.productListUrl, productListSearchUrl: props.productListSearchUrl })
    const [imgData, imgDispatch] = useReducer(imgReducer, { images: [], fetching: true, })

    let bottomBoundaryRef = useRef(null);
    useFetch(pager, imgDispatch);
    useLazyLoading('.card-img-top', imgData.images)
    useInfiniteScroll(bottomBoundaryRef, pagerDispatch);

    function renderRating(product) {
        var rating = 5;
        if (product.rating !== undefined) {
            rating = parseFloat(product.rating).toFixed(1)
        }
        if (rating === 5) {
            return (
                <div class="stars-wrapper stars-main justify-content-center">
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <div class="pl-2 font-weight-bold text-white">{rating} out of 5</div>
                </div>
            );
        } else if (rating > 4) {
            return (
                <div class="stars-wrapper stars-main justify-content-center">
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <img width='16' height='15' src='/icons/white-half-star.svg' alt='five star ratings' />
                    <div class="pl-2 font-weight-bold text-white">{rating} out of 5</div>
                </div>
            );
        } else if (rating === 4) {
            return (
                <div class="stars-wrapper stars-main justify-content-center">
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <div class="pl-2 font-weight-bold text-white">{rating} out of 5</div>
                </div>
            );
        } else if (rating > 3) {
            return (
                <div class="stars-wrapper stars-main justify-content-center">
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <img width='16' height='15' src='/icons/white-half-star.svg' alt='five star ratings' />
                    <div class="pl-2 font-weight-bold text-white">{rating} out of 5</div>
                </div>
            );
        } else if (rating === 3) {
            return (
                <div class="stars-wrapper stars-main justify-content-center">
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <div class="pl-2 font-weight-bold text-white">{rating} out of 5</div>
                </div>
            );
        } else if (rating > 2) {
            return (
                <div class="stars-wrapper stars-main justify-content-center">
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <img width='16' height='15' src='/icons/white-half-star.svg' alt='five star ratings' />
                    <div class="pl-2 font-weight-bold text-white">{rating} out of 5</div>
                </div>
            );
        } else if (rating === 2) {
            return (
                <div class="stars-wrapper stars-main justify-content-center">
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <div class="pl-2 font-weight-bold text-white">{rating} out of 5</div>
                </div>
            );
        } else if (rating > 1) {
            return (
                <div class="stars-wrapper stars-main justify-content-center">
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <img width='16' height='15' src='/icons/white-half-star.svg' alt='five star ratings' />
                    <div class="pl-2 font-weight-bold text-white">{rating} out of 5</div>
                </div>
            );
        } else if (rating === 1) {
            return (
                <div class="stars-wrapper stars-main justify-content-center">
                    <img width='16' height='15' src='/icons/white-star.svg' alt='five star ratings' />
                    <div class="pl-2 font-weight-bold text-white">{rating} out of 5</div>
                </div>
            );
        }
    }

    return (
        <div class="card-columns p-2 ml-5">
            <div>Home Page</div>
            <div id='page-bottom-boundary' style={{ border: '1px solid red' }} ref={bottomBoundaryRef}></div>
        </div>
    );
}

export default Products;